import React from 'react'

import ScrollableAnchor from 'react-scrollable-anchor'
import LazyLoad from 'react-lazy-load'

import FaUserSecret from 'react-icons/lib/fa/user-secret'
import FaUpload from 'react-icons/lib/fa/upload'

import '../sass/general.scss'

export const General = () =>

    <ScrollableAnchor id={'home'}>
    <div className="general" id="home1">
        <div className="container">
            <div className="row">
                <div className="col-sm-12">
                    <div className="col-sm-12 extend-line">
                        <LazyLoad height={8}  className="general-lazy">
                        <h2 className="text-center main"><span>CO WEB DESIGNS</span></h2>
                        </LazyLoad> 
                    </div>
                    <div className="row about-row">
                        <div className="col-sm-4">
                         <LazyLoad height={36} className="whoiam">
                            <h4>Who I am <span><FaUserSecret /></span></h4>
                        </LazyLoad> 
                        </div>
                        <div className="col-sm-8">
                            <p>Hi, my name is Cody. I am a freelance web designer, developer and content strategist. My specialities lie in branding and website conversions that lead to an increased online presence. An engineer by trade, my passions are entrepreneurship and chess.</p>
                                
                            <p>With many years expereience making websites for industries ranging from fashion to non-porfits, my knowledge of the online landscape is vast.</p>
                        </div>
                    </div>
                    <div className="row about-row">
                        <div className="col-sm-4">
                            <LazyLoad height={36} className="whatido">
                                <h4>What I do <span><FaUpload /></span></h4>
                            </LazyLoad> 
                        </div>
                        <div className="col-sm-8">
                            <p>I work on the full stack of web development. On the frontend I excel at HTML, Adobe, CSS(Sass), Jquery, and Wordpress. On the backend I excel at Javascript, PHP, and database implementation. A lot of jargon, I know.</p>
                                
                            <p>Basically I can build any kind of fully functional website and make it look really nice. If you are looking for a website at an affordable price I'm your man.</p>
                        </div>
                    </div>
                </div>
            </div> 
        </div> 
    </div>
    </ScrollableAnchor> 


