import React from 'react'

import { Component } from 'react'
import { Link } from 'react-router-dom'
import ScrollableAnchor from 'react-scrollable-anchor'
import LazyLoad from 'react-lazy-load'


import FaMobile from 'react-icons/lib/fa/mobile'
import FaCheck from 'react-icons/lib/fa/check'
import FaMapMarker from 'react-icons/lib/fa/map-marker'
import FaGroup from 'react-icons/lib/fa/group'
import FaInstagram from 'react-icons/lib/fa/instagram'
import FaLinkedinSquare from 'react-icons/lib/fa/linkedin-square'
import FaFacebookSquare from 'react-icons/lib/fa/facebook-square'
import MdEmail from 'react-icons/lib/md/email'

import '../sass/contact.scss'

export class Contact extends Component {

    constructor(props) {
        super(props)
        this.submit = this.submit.bind(this)
    }

submit(e) {

    e.preventDefault()
    let name = this.refs.name,
        email = this.refs.email,
        message = this.refs.message

    const $ = require ('jquery')
            
    function validEmail(email) { // see:
    var re = /^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/i
    return re.test(email)
    }

  // we are submitting via xhr below
    const data = {
      name: name.value,
      email: email.value,
      message: message.value
  }         // get the values submitted in the form
  if( !validEmail(data.email) ) {   // if email is not valid show error
   $(this.invalid_email).attr( 'style', 'display:' + ' ' + 'block;' )
    return false

  } else {
      
    let url = this.form.action 
    let xhr = new XMLHttpRequest()
    xhr.open('POST', url)
    // xhr.withCredentials = true
    xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded')

    xhr.onreadystatechange = function() {
        console.log( xhr.status, xhr.statusText )
        console.log(xhr.responseText)
       // $(this.form).reset()
        return
    }
    // url encode form data for sending as post data
    let encoded = Object.keys(data).map(function(k) {
        return encodeURIComponent(k) + '=' + encodeURIComponent(data[k])
    }).join('&')
    xhr.send(encoded)


    if( xhr.send ) {
        $(this.invalid_email).attr( 'style', 'display:' + ' ' + 'none;' )
        $(this.thankyou_message).attr( 'style', 'display:' + ' ' + 'block;' )
        this.form.name.value = ""
        this.form.email.value = ""
        this.form.message.value = ""
    }
  }   
}

    render() {


        return (
            
            <ScrollableAnchor id={'contact'}>
            <div className="contact" id="contact1">
                <div className="copyright mt-auto mb-auto">
                    <p>All rights reserved &copy; COWebDesigns 2017</p>
                </div>
                <div className="container">
                    <div className="row">
                        <div className="col-sm-12">
                            <h2 className="main-contact">
                                <span>CONTACT</span>
                                <span className="bottom-border"></span>
                            </h2>
                            <div className="row">
                                <div className="col-sm-12 col-md-4 personal">

                                    <LazyLoad height={25} className="phone">
                                    <h5><span><FaMobile /></span> Phone</h5>
                                    </LazyLoad> 
                                    <p>973-979-4956</p>

                                    <LazyLoad height={25} className="email">
                                    <h5><span><MdEmail /></span> Email</h5>
                                    </LazyLoad>
                                    <p>info@cowebdesigns.com</p>

                                    <LazyLoad height={25} className="location">
                                    <h5><span><FaMapMarker /></span> Location</h5>
                                    </LazyLoad> 
                                    <p>Bloomfield, NJ</p>
                                    
                                    <LazyLoad height={25} className="connect"> 
                                    <h5><span><FaGroup /></span> Connect</h5>
                                    </LazyLoad> 
                                    <p>
                                        <a href="https://www.instagram.com/cody.okafor" target="_blank" alt="Instagram"><FaInstagram /></a>
                                        <a href="https://www.facebook.com/cody.okafor" target="_blank" alt="Facebook"><FaFacebookSquare /></a>
                                        <a href="https://www.linkedin.com/in/codyokafor" target="_blank" alt="LinkedIn"><FaLinkedinSquare /></a>
                                    </p>
                                </div>
                                <div className="col-sm-12 col-md-8 contactform">

                                    <p className="display_none error" ref={ (ref) => this.invalid_email = ref } id="email-invalid">Whoops, something went wrong please check your email</p>
                                    <p className="display_none success" ref={ (ref) => this.thankyou_message = ref } id="thankyou_message"><span><FaCheck /></span>Thank you! I will get back to you soon</p> 

                                    <form onSubmit = { this.submit } 
                                          className="form" 
                                          ref={ (ref) => this.form = ref }
                                          action="https://script.google.com/macros/s/AKfycbwzHaxll0NXm3apfYwFQxz-C35bmShqrrocta9MeJ2cuDzQINfn/exec"
                                          method="POST"
                                          >
                                    
                                        <div className="row">
                                            <div className="col-md-6 col-sm-12">
                                                <div className="form-group row">
                                                    <label htmlFor="name" className="sr-only">Name</label>
                                                    <input  className="form-control"
                                                            id="name" 
                                                            type="text"
                                                            name="name"
                                                            placeholder="Name" 
                                                            required 
                                                            ref="name" />
                                                </div>
                                            </div>
                                                
                                            <div className="col-md-6 col-sm-12">
                                                <div className="form-group row">
                                                    <label htmlFor="email" className="sr-only">Email</label>
                                                    <input  className="form-control"
                                                            id="email" 
                                                            type="text"
                                                            name="email"
                                                            placeholder="Email" 
                                                            required 
                                                            ref="email" />         
                                                </div>
                                            </div>     

                                            <div className="col-sm-12">
                                                <div className="form-group row">
                                                    <label htmlFor="message" className="sr-only">Message</label>
                                                    <textarea   className="form-control"
                                                                id="message"
                                                                name="message"
                                                                placeholder="Message" 
                                                                required 
                                                                ref="message" />         
                                                </div>
                                            </div>    

                                            <div className="col-sm-12">
                                                <button className="btn btn-primary btn-lg" type="submit">Submit</button>
                                            </div>
                                               
                                        </div>

                                    </form>

                                </div>
                            </div>

                            <div className="row outro">
                                <div className="col-sm-12 col-md-4 headshot">
                                    <LazyLoad height={312} className="portrait">
                                    <img src="http://dq21c15nyx2l2.cloudfront.net/portfolio/portrait.png" className="img-fluid" />
                                    </LazyLoad> 
                                </div>
                                <div className="col-sm-12 col-md-8 excerpt">
                                    <p>Fully functional top quality website and digital solutions without breaking the bank. I am open to all inquiries. Connect with me or just shoot me a message. Thanks for visiting and lets work together!</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </ScrollableAnchor >
            
        )
    }
}


