import React from 'react'

import ScrollableAnchor from 'react-scrollable-anchor'
import LazyLoad from 'react-lazy-load'

import FaSearchPlus from 'react-icons/lib/fa/search-plus'

import '../sass/work.scss'

export const MyWork = () =>

    <ScrollableAnchor id={'work'}>
    <div className="work" id="work1">
        <div className="container">
            <div className="row">
                <div className="col-sm-12">
                    <div className="col-sm-12 extend-line">
                        <LazyLoad height={8} className="work-lazy">
                        <h2 className="text-center main"><span>MY WORK</span></h2>
                        </LazyLoad> 
                    </div>
                    <div className="row work-row">
                        <ul className="list-group">
                            <a data-content="design / development / wordpress" href="https://kelcherpromotions.com/" target="_blank"><i><FaSearchPlus /></i>
                                <li className="list-group-item"><img className="img-fluid" src="http://dq21c15nyx2l2.cloudfront.net/portfolio/kelcher.png"/></li>
                            </a>
                            <a data-content="design / development / wordpress" href="#" target="_blank"><i><FaSearchPlus /></i>
                                <li className="list-group-item"><img className="img-fluid" src="../images/ace.png"/></li>
                            </a>
                            <a data-content="design / development" href="http://aahdevelopers.com/" target="_blank"><i><FaSearchPlus /></i>
                                <li className="list-group-item"><img className="img-fluid" src="http://dq21c15nyx2l2.cloudfront.net/portfolio/aah.png"/></li>
                            </a>
                            <a data-content="design / development" href="#" target="_blank"><i><FaSearchPlus /></i>
                                <li className="list-group-item"><img className="img-fluid" src="http://dq21c15nyx2l2.cloudfront.net/portfolio/maccomm.png"/></li>
                            </a>
                           <a data-content="design" href="http://dokita247.com/" target="_blank"><i><FaSearchPlus /></i>
                                <li className="list-group-item"><img className="img-fluid" src="http://dq21c15nyx2l2.cloudfront.net/portfolio/dokita.png"/></li>
                            </a>
                            <a data-content="design / development / ecommerce" href="https://mypresidentshirts.com/" target="_blank"><i><FaSearchPlus /></i>
                                <li className="list-group-item"><img className="img-fluid" src="http://dq21c15nyx2l2.cloudfront.net/portfolio/president.png"/></li>
                            </a>
                            <a data-content="design / development" href="#" target="_blank"><i><FaSearchPlus /></i>
                                <li className="list-group-item"><img className="img-fluid" src="http://dq21c15nyx2l2.cloudfront.net/portfolio/capture.png"/></li>
                            </a>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </ScrollableAnchor> 


