import React from 'react'

import { Component } from 'react'
import { Link } from 'react-router-dom'
import { goToAnchor } from 'react-scrollable-anchor'
import { goToTop } from 'react-scrollable-anchor'
import { configureAnchors } from 'react-scrollable-anchor'
import Scrollspy from 'react-scrollspy'

import FaHome from 'react-icons/lib/fa/home'
import FaBriefcase from 'react-icons/lib/fa/briefcase'
import FaArrowCircleDown from 'react-icons/lib/fa/arrow-circle-down'

import '../sass/header.scss'

export class Header extends Component {

constructor(props) {
    super(props)
    this.navScroll = this.navScroll.bind(this)
}

  componentDidMount() {
    window.addEventListener('scroll', this.navScroll)
    configureAnchors({ offset: -60, scrollDuration: 1000 })
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.navScroll)
  }

  navScroll(e) {
    let scrollTop = window.event.target.body.scrollTop
    const $ = require ('jquery')

    if ( this.navbar !== null && this.logo !== null ) {
        if( $(document).scrollTop() > 50  ) {
            $(this.navbar).attr( 'class', 'navbar navbar-toggleable-md navbar-light fixed-top addbackground' )
            $(this.logo).attr( 'class', 'nav logo mt-auto resize' )
        }
        else {
            $(this.navbar).attr( 'class', 'navbar navbar-toggleable-md navbar-light fixed-top' )
            $(this.logo).attr( 'class', 'nav logo mt-auto' )
        }
    }
  }

    render () {

        return (

            <div className="header">
                <nav ref={(ref) => this.navbar = ref} className="navbar navbar-toggleable-md navbar-light fixed-top">
                    <div>
                        <button type="button" className="btn navbar-toggler pull-left" data-toggle="collapse" data-target="#navbarToggle" aria-controls="navbarToggle" aria-expanded="false" aria-label="Toggle navigation"> <span className="navbar-toggler-icon"></span></button>
                    </div>
                    <span ref={(ref) => this.logo = ref} className="nav logo mt-auto" onClick={ goToTop }>
                        <img src="http://dq21c15nyx2l2.cloudfront.net/portfolio/logo.png" alt="logo" className="img-fluid"/>
                    </span>
                    <div className="mb-auto collapse navbar-collapse" id="navbarToggle">
                        <Scrollspy items={ ['home1', 'work1', 'services1', 'contact1'] } 
                                   currentClassName="is-current" 
                                   className="nav navbar-nav"
                                   offset={ -70 }
                                   >
                            <li className="nav-item active">
                                <Link className="nav-link" to='#home' onClick={ goToAnchor }><span><FaHome /></span></Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to='#work' onClick={ goToAnchor }>WORK</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to='#services' onClick={ goToAnchor }>SERVICES</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link" to='#contact' onClick={ goToAnchor }>CONTACT</Link>
                            </li>
                        </Scrollspy>
                    </div>
                </nav>
                <section className="section">
                    <div className="center">
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-sm-12 headline">
                                    <p className="cv">Web Developer - Web Designer - Eccomerce</p>
                                    <p className="tag">Custom Built Web Solutions</p>
                                    <p className="capture">Define your brand and</p>
                                    <p className="capture">Realize your vision with a beautifully built website</p>
                                    <Link to='#work' onClick={ goToAnchor }>
                                        <button className="btn-lg btn-primary">View My Work <span><FaBriefcase /></span></button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                    <p><Link to='#home' onClick={ goToAnchor }><FaArrowCircleDown /></Link></p>
                </section>
            </div>
        )
    }
}


